import { Component, OnInit, Optional, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import icClose from '@iconify/icons-ic/twotone-close';
import { DomSanitizer } from '@angular/platform-browser';
@Component({
  selector: 'app-phieu-tra-vat-tu-tu-benh-nhan-popup',
  templateUrl: './phieu-tra-vat-tu-tu-benh-nhan-popup.component.html',
  styleUrls: ['./phieu-tra-vat-tu-tu-benh-nhan-popup.component.scss']
})
export class PhieuTraVatTuTuBenhNhanPopupComponent implements OnInit {
  icClose = icClose;
  src: string = "";
  constructor(private dialog: MatDialog, public sanitizer: DomSanitizer,
    private dialogRef: MatDialogRef<PhieuTraVatTuTuBenhNhanPopupComponent>,

    @Optional() @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit() {
    this.src = "data:text/html;charset=utf-8," + encodeURIComponent(this.data);
  }
  close() {
    this.dialogRef.close();
  }
  getSharedPrintForm() {
    return new Promise(resolve => {
      resolve(this.data);
    });
  }

}
